<template>
   
    <div class="d-inline">

        <div class="tree-table-content"  :class="{ 'tree-root': the_item.index < 2 }">
            <div class="unid tree-col">
                {{ the_item.unid }}
            </div>
            <div class="layer tree-col">
                {{ the_item.index }}
            </div>
            <div class="ipv tree-col">
                {{ show_pv }}
            </div>
            <div class="level tree-col">
                {{ show_vip }}
            </div>
            <div class="son_pv tree-col">
                
            </div>
            <div class="can_num tree-col flex-center">
                
                <user-newer-change :unid="the_item.unid" :is_new="the_item.is_new" :id="the_item.id"></user-newer-change>

            </div>
            <div class="result tree-col  fs-15">
                {{ this.usd }}
            </div>
        </div>
    </div>
</template>

<script>
import bonus_recommend from '@/common/es/bonus/bonusApi/bonus_recommend'
import UserNewerChange from '../../../../components/Form/UserNewerChange.vue'

    export default {
  components: { UserNewerChange },
        name: '',
        props: {
            the_item: {
                type: Object
            },
            allow: {
                type: Boolean
            }
        },
        name: '',
        data() {
            return {
                num: 0,
                vip: '5级会员',
                usd: 0,
                ipv: 0,

            }
        },
        async mounted() {
            if (this.allow) {
                this.usd = await this.load_data()
            }
        },
        computed: {
            num_color() {
                if (this.num > 0) {
                    return true
                }
                return false
            },
            show_vip() {
                if (this.the_item.vip) { return this.the_item.vip.name }
                return ''
            },
            show_pv() {
                if (this.the_item.vip) { return this.the_item.ipv }
                return '待计算'
            },
            show_son_pv() {
                if (this.the_item.vip) { return this.the_item.son_pv }
                return ''
            },
            is_show() {
                let res = false
                if (this.the_item.vip) { res = true }
                return res
            }
        },
        methods: {
            async _numing() {

                const res = await bonus_recommend(this.the_item)
                return res[0] + ', ' + res[1] + ', ' + res[2]
            },
            load_data() {
                const self = this
                return new Promise((rej, rev) => {
                    let res = 0

                    res = this._numing()
                    rej(res)
                })
            },
        }
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/table_header.sass" 
.usd
    color: #607d8b!important
</style> 


