import common from './common'
import util from '../util'

import conf from '../conf'
const NAMESPACE = conf.NAMESPACE
const CAN_USER_PV_LIMIT = conf.CAN_USER_PV_LIMIT

// 判断是否 是新手
const jugde_new = function(user_item) {

    if (user_item[
            NAMESPACE.is_new
        ]
    ) {
        return true
    }
    return false
}

// 判断本月是否 购买
const judge_buy = function(items) {
    if (
        items[
            NAMESPACE.buys
        ] && 
        items[
            NAMESPACE.buys
        ].length >= 0
    ) {
        return true
    }
    return false
}


const ser_users_from_tree = function(user,  name_field = NAMESPACE.children) {
    
    return new Promise((rej, rev) => {
        let user_3 = []
        let user_2 = []
        let user_1 = []

        if (util.judge_has_children(user)) {
            
            // 1代
            const child_one = user[name_field]
            child_one.map(e => {

                if (util.judge_has_children(e)) {

                    // 2代
                    const child_two = e[name_field]
                    child_two.map(f => {

                        if (util.judge_has_children(f)) {
                            user_3.push(...f[name_field])
                        }
                    })
                    user_2.push(...child_two)
                }
                user_1.push(e)
            })

        }

        rej(
            { user_1, user_2, user_3 }
        )
    })
    
}

// 计算结果
const doingResult = function(res, ratios) {
    return [
        res[0] * ratios[0],
        res[1] * ratios[1],
        res[2] * ratios[2]
    ]
}

const doing = function(user_0, user_1, user_2, user_3) {

    let pv = 0
    let res = [ 0, 0, 0]

    return new Promise((rej, rev) => {

        // 0代
        pv = util.numd_pv(user_0)

        if (pv > 0.5) {

            // 1代
            user_1.map(e => {

                if (
                    judge_buy(e) && 
                    e[ NAMESPACE.is_new ]
                ) {
                    
                    res[0] += util.numd_pv(e)
                }
            })
            // 2代
            user_2.map(e => {

                if (
                    judge_buy(e) && 
                    e[ NAMESPACE.is_new ]
                ) {
                    
                    res[1] += util.numd_pv(e)
                }
            })
            // 3代
            user_3.map(e => {

                if (
                    judge_buy(e) && 
                    e[ NAMESPACE.is_new ]
                ) {
                    
                    res[2] += util.numd_pv(e)
                }
            })
        }

        rej(res)
    })
}
// user = Object
const numing_recommend_usd = async function(user, ratios = [ 0.2, 0.1, 0.05 ]) {

    const { user_1, user_2, user_3 } = await ser_users_from_tree(user)

    let res = []

    if (user_3) {
        res = await doing(user, user_1, user_2, user_3)

        res = doingResult(res, ratios)
    }

    return res
}

export default numing_recommend_usd


