<template>
    
    <tree-layout ref="treeLayout" :isUM="false" @numing_Father="numing" @user_Father="user">

        <alb-table-header slot="tree_header"></alb-table-header>

        <v-treeview multiple-active transition dense off-icon slot="tree"
            class="mt-0 the-tree fs-14"

            :items="users" open-all :open="[0, 1]"
            v-if="show"
        >
            <template v-slot:label="{ item, open }">
                <name-label :the_item="item" :op="open"></name-label>
            </template>
            <template v-slot:append="{ item, open }">
                <alb-tree-item ref="treeItem" :allow="can_num" :the_item="item" :o="open"></alb-tree-item>
            </template>
        </v-treeview>
    </tree-layout>

</template>

<script>

import rolling_api from '@/common/es/bonus/rolling_api'

import user_data from '@/apis/mock/user_unit_data'
import user_relation from '@/common/es/bonus/userApi/user_relation'

import TreeLayout from '@/components/Layout/TreeLayout.vue'

import AlbTableHeader from './AlbTableHeader.vue'
import AlbTreeItem from './AlbTreeItem.vue'
import NameLabel from '@/components/Tree/NameLabel.vue'

    export default {
        components: { TreeLayout, AlbTableHeader, AlbTreeItem, NameLabel },
        name: '',
        data() {
            return {
                users: [ ],
                show: false,
                can_num: false
            }
        },
        mounted() {
            this.user('ENROLLER')
        },
        methods: {
            reloadTree(v) {
                if (this.$refs.treeLayout != undefined) {
                    this.show = v
                    this.$refs.treeLayout.show = v
                }
            },

            numing() {
                this.reloadTree(false)

                this.can_num = true
                
                setTimeout(() => {
                    this.reloadTree(true)
                }, 1000)
            },
            async user(user_code) {
                
                this.reloadTree(false)
                
                this.users = await user_relation(this.$store.state.user_unit, user_code)

                let ul = Array.from(this.users)
                rolling_api(ul)

                setTimeout(() => {
                    this.reloadTree(true)
                }, 1000)
            },
        }
    }
</script>

<style lang="sass" scoped>
        
</style>


