<template>
    <span @click="newer">
        {{ show_newer }}
    </span>
</template>

<script>
    export default {
        name: '',
        props: {
            id: {
                type: String
            },
            unid: {
                type: String
            },
            is_new: {
                type: Boolean
            }
        },
        data() {
            return {
                
            }
        },
        computed: {
            show_newer() {
                if (this.is_new) { return 'yes' }
                return 'no'
            }
        },

        methods: {
            async newer() {

                let res = this.$store.state.user_unit.map(e => {
                    if (e.unid == this.unid) {
                        e.is_new = !e.is_new
                    }

                    return e
                })

                let data = {
                    is_new: !this.is_new
                }

                data = await this.conn.put(
                    this.api.users + '/' + this.id,
                    this.$store.state.token,
                    data
                )

                if (data) {

                    this.$store.commit('saveUnitUser', res)
                }
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


